.App {
  text-align: center;
  background-color: rgb(139, 30, 52);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.hello {
  min-height: 100vh; /* height of the browser viewport */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.aboutMe {

  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-left: 25%;
  margin-right: 25%;
}

.headshot {
  width: 40%;
  height: 40%;
  border-radius: 10%;
}

.techskills {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.languages {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}

.language {
  height: 10%;
  width: 10%;
  opacity: 0.7;
  transition: 0.3s;
}

.projects {
  min-height: 80vh;
  display : flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.navbar-custom {
  background-color: crimson;
}

.fundiesProjects {
  display : flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}

.project {
  width: 30%;
  height: 30%;
  transition: 0.3s;
}

.project:hover{opacity : 0.7}


.language:hover {opacity: 1}

.typingAnimations {
  animation-iteration-count:infinite;
}